.logo img{
    width:10%;
}

navbar{
    display:flex;
    align-items: center;
    padding:0.2% 5%;
    justify-content: space-between;
}

navbar ul{
    display:flex;
    list-style: none;
}

navbar ul li{
    padding:0 20px;
    cursor: pointer;
    font-weight:600;
}

navbar ul li:hover{
    color:blueviolet;
    transition: 0.2s;
}

.body{
    padding: 0 15%;
    padding-top:5%;
    padding-bottom:2%;
}

.body h1{
    font-weight: 800;
    font-size: 3rem;
    padding-bottom:1%;
}

@media (max-width:980px){
    .logo img{
        width:20%;
    }
    navbar{
        font-size: 180%;
    }
    .body h1{
        font-size: 6rem;
    }
    .body p{
        font-size: 1.1rem;
    }
    .body{
        padding-top:15%;
        padding-bottom:10%;
    }
}