form{
    display:flex;
    flex-direction: column;
}

form .finput, .sinput{
    margin:0.8% 0;
    padding:0.6% 0;
}

form .tinput{
    padding:2% 0;
    margin:0.8% 0;
}

.hide{
    display:none;
}

.visible{
    display:block;
}

.fbox{
    position:relative;
}

.fbox h1{
    font-weight: 700;
    font-size: 0.8rem;
    position: absolute;
    top:2%;
    left:1%;
    padding:0.1% 0.3%;
}

.sbox{
    position:relative;
}

.sbox h1{
    font-weight: 700;
    font-size: 0.8rem;
    position: absolute;
    top:2%;
    left:1%;
    padding:0.1% 0.3%;
}

.tbox{
    position:relative;
}

.tbox h1{
    font-weight: 700;
    font-size: 0.8rem;
    position: absolute;
    top:2%;
    left:1%;
    padding:0.1% 0.3%;
}

.main-container{
    display:flex;
    padding:0 15%;
}

.main-container form{
    width:50%;
}

.main-container .image-container{
    width:50%;
}

.image-container img{
    width:100%;
    height:100%;
}

.finput, .sinput, .tinput{
    width:100%;
    height:80%;
}

.fbox, .sbox{
    height:20%;
}

.tbox{
    height:50%;
}

.main-container form{
    position: relative;
}

.main-container button{
    width: 50%;
    position: absolute;
    right:0;
    bottom:0;
    padding:2% 0;
    border:black solid 1.5px;
    color:white;
    background-color: black;
    font-size:800;
}

.main-container button:hover{
    color:black;
    background-color: white;
    transition: 0.3s;
}

.result{
    padding:5% 15%;
    font-size:2rem;
    font-weight: 300;
    position:absolute;
}

@media(max-width:980px){
    .main-container{
        flex-direction: column;
        
    }
    .main-container form{
        width:100%;
        order:2;
    }
    .main-container .image-container{
        width:100%;
        order:1;
    }
    .fbox h1{
        font-size: 2rem;
    }
    .finput{
        height:8rem;
        font-size: 2rem;
    }
    .sbox h1{
        font-size: 2rem;
    }
    .sinput{
        height:8rem;
        font-size: 2rem;
    }
    .tbox h1{
        font-size: 2rem;
    }
    .tinput{
        height:18rem;
        font-size: 2rem;
    }
    .main-container button{
        bottom:-15%;
        font-size: 2rem;
        border:none;
    }
    .result{
        padding-top:20%;
    }
}